/* src/components/Footer/Footer.css */
.footer {
 background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.8)), url('http://escandalosospizzas.cl/wp/wp-content/uploads/2025/08/Diseno-sin-titulo.png') center/cover;
 color: white;
 margin-top: auto;
}

.footer-main {
  padding: 4rem 0 3rem;
}

.footer-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  gap: 3rem;
}

.footer-column {
  display: flex;
  flex-direction: column;
}

/* Brand Column */
.footer-brand {
  font-size: 1.75rem;
  font-weight: 900;
  color: var(--primary-color);
  margin-bottom: 0.25rem;
}

.footer-tagline {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #999;
  margin-bottom: 1rem;
}

.footer-description {
  color: #ccc;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  color: white;
  transition: all 0.3s;
}

.social-link:hover {
  background: var(--primary-color);
  transform: translateY(-2px);
}

/* Footer Title */
.footer-title {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: white;
  position: relative;
}

.footer-title::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  width: 40px;
  height: 2px;
  background: var(--primary-color);
}

/* Footer Links */
.footer-links {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.footer-links a {
  color: #ccc;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: var(--primary-color);
}

/* Contact Info */
.contact-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #ccc;
}

.contact-item svg {
  color: var(--primary-color);
  flex-shrink: 0;
}

/* Newsletter */
.footer-text {
  color: #ccc;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.newsletter-form {
  display: flex;
  gap: 0.5rem;
}

.newsletter-input {
  flex: 1;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 0.875rem;
  transition: all 0.3s;
}

.newsletter-input::placeholder {
  color: #999;
}

.newsletter-input:focus {
  outline: none;
  border-color: var(--primary-color);
  background: rgba(255, 255, 255, 0.15);
}

.newsletter-btn {
  padding: 0.75rem 1.5rem;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
}

.newsletter-btn:hover {
  background: var(--primary-hover);
  transform: translateY(-2px);
}

/* Footer Bottom */
.footer-bottom {
  background: #000;
  padding: 1.5rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.copyright {
  text-align: center;
  color: #999;
  font-size: 0.875rem;
}

/* Responsive */
@media (max-width: 968px) {
  .footer-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media (max-width: 576px) {
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .footer-main {
    padding: 3rem 0 2rem;
  }

  .newsletter-form {
    flex-direction: column;
  }

  .newsletter-btn {
    width: 100%;
  }
}
/* Agregar estos estilos a Footer.css */

/* Horarios */
.schedule-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-left: 0.5rem;
}

.schedule-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.875rem;
}

.schedule-day {
  font-weight: 500;
  color: var(--text-light);
  min-width: 80px;
}

.schedule-time {
  color: var(--text-light);
  opacity: 0.9;
}

/* Ajuste para el ícono del reloj */
.contact-item:has(.schedule-list) {
  align-items: flex-start;
}

.contact-item:has(.schedule-list) > svg {
  margin-top: 0.125rem;
}

/* Responsive para móviles */
@media (max-width: 768px) {
  .schedule-item {
    font-size: 0.8125rem;
  }
  
  .schedule-day {
    min-width: 70px;
  }
}